@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.5.0/font/bootstrap-icons.css);
@import url(https://fonts.googleapis.com/css2?family=Lateef&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-image:linear-gradient(to right, red, purple);padding-top:10px;font-family:'Lateef', cursive}.wrapper{padding:3rem 0;background-color:#212529;min-height:100vh}.wrapper .form-control:focus{box-shadow:none;border-color:#198754}.wrapper .wrapperColorbox{margin-top:4rem;display:none}.wrapper .wrapperColorbox .colorBox{aspect-ratio:1/1;display:grid;place-items:center;position:relative}.wrapper .wrapperColorbox .colorBox span{color:#2b2b2b;background-color:white;padding:0 .5rem}.wrapper .error-para{transition:all .3s ease}@media screen and (min-width: 768px){.cursor{scroll-behavior:smooth;width:15px;height:15px;border-radius:50%;background-color:crimson;transition:width .3s ease, height .3s ease}.scale{width:100px;height:100px;background-color:black;text-align:center;color:white;display:grid;place-items:center;mix-blend-mode:normal;font-size:0.8rem}.blend-mode-img{background-color:white;mix-blend-mode:difference;width:80px;height:80px}}

